import React from 'react';
import TextField from '@material-ui/core/TextField';
import Button from '@material-ui/core/Button';
// import Grid from '@material-ui/core/Grid'

const initialState = {
  values: {
    yourName: '',
    surname: '',
    username: '',
    email: '',
    password: '',
  },
  errors: {
    yourName: '',
    surname: '',
    username: '',
    email: '',
    password: '',
  },
  invalid: {
    yourName: null,
    surname: null,
    username: null,
    email: null,
    password: null,
  },
  touched: {
    yourName: false,
    surname: false,
    username: false,
    email: false,
    password: false,
  },
};

class Form extends React.Component {
  constructor() {
    super();
    this.state = initialState;
  }

  change = (e) => {
    const { name, value } = e.target;
    this.props.onChange({ [name]: value });
    this.setState({
      values: {
        ...this.state.values,
        [name]: value,
      },
    });
    this.handleState(this.validateEmpty(name, value));
    if (name === 'username') this.validateUsername();
    if (name === 'email') this.validateEmail();
    if (name === 'password') this.validatePassword();
  };

  validateUsername = () => {
    if (this.state.values.username.length < 5 && this.state.values.username.length > 0) {
      this.setState({
        errors: {
          ...this.state.errors,
          username: 'Username must be at least 6 characters long',
        },
        invalid: {
          ...this.state.invalid,
          username: true,
        },
      });
    }
  }

  validateEmail = () => {
    if (!this.state.values.email.match(/^([\w.%+-]+)@([\w-]+\.)+([\w]{2,})$/i) && this.state.values.email.length > 0) {
      this.setState({
        errors: {
          ...this.state.errors,
          email: 'Email address is not valid',
        },
        invalid: {
          ...this.state.invalid,
          email: true,
        },
      });
    }
  }

  validatePassword = () => {
    if (this.state.values.password.length < 6 && this.state.values.password.length > 0) {
      this.setState({
        errors: {
          ...this.state.errors,
          password: 'Password must be at least 6 characters long',
        },
        invalid: {
          ...this.state.invalid,
          password: true,
        },
      });
    }
  }

  validate = () => {
    this.validateUsername();
    this.validateEmail();
    this.validatePassword();
  }

  onSubmit = (e) => {
    e.preventDefault();
    const errors = { ...this.state.errors };
    const invalid = { ...this.state.invalid };
    const newObj = { errors, invalid };
    // this.props.onSubmit(this.state);
    // this.validate();
    Object.keys(this.state.invalid).forEach((i) => {
      if (this.state.invalid[i] !== false && this.state.values[i].length === 0) {
        newObj.errors[i] = this.validateEmpty(i, this.state.values[i]).errors[i];
        newObj.invalid[i] = this.validateEmpty(i, this.state.values[i]).invalid[i];
      }
      this.handleState(newObj);
    });
    this.validate();
    const {
      yourName, surname, username, email, password,
    } = this.state.invalid;
    if (yourName === false && surname === false
      && username === false && email === false
      && password === false) {
      alert('Registered succesfully');
      this.setState(
        initialState,
      );
      this.props.onChange({
        yourName: '',
        surname: '',
        username: '',
        email: '',
        password: '',
      });
    }
  };

  handleBlur = (e) => {
    const { name, value } = e.target;
    this.setState({ touched: { ...this.state.touched, [name]: true } });
    this.handleState(this.validateEmpty(name, value));
    if (name === 'username') this.validateUsername();
    if (name === 'email') this.validateEmail();
    if (name === 'password') this.validatePassword();
  }

  validateEmpty = (field, value) => {
    const errors = { ...this.state.errors };
    const invalid = { ...this.state.invalid };
    const obj = { errors, invalid };
    if (!value.trim()) {
      errors[field] = 'This field is required';
      invalid[field] = true;
    } else {
      errors[field] = '';
      invalid[field] = false;
    }
    return obj;
  }

  handleState = (obj) => {
    this.setState({
      errors: obj.errors,
      invalid: obj.invalid,
    });
  }

  render() {
    return (
        <form>
          <TextField
            name="yourName"
            label="Your Name"
            value={this.state.values.yourName}
            onChange={e => this.change(e)}
            error={this.state.invalid.yourName}
            helperText={this.state.errors.yourName}
            fullWidth
            onBlur={e => this.handleBlur(e)}
          />
          <br />
          <TextField
            name="surname"
            label="Surname"
            value={this.state.values.surname}
            onChange={e => this.change(e)}
            error={this.state.invalid.surname}
            helperText={this.state.errors.surname}
            onBlur={e => this.handleBlur(e)}
            fullWidth
          />
          <br />
          <TextField
            name="username"
            label="Username"
            value={this.state.values.username}
            onChange={e => this.change(e)}
            error={this.state.invalid.username}
            helperText={this.state.errors.username}
            onBlur={e => this.handleBlur(e)}
            fullWidth
          />
          <br />
          <TextField
            name="email"
            label="E-mail address"
            value={this.state.values.email}
            onChange={e => this.change(e)}
            type="email"
            error={this.state.invalid.email}
            helperText={this.state.errors.email}
            onBlur={e => this.handleBlur(e)}
            fullWidth
          />
          <br />
          <TextField
            name="password"
            label="Password"
            value={this.state.values.password}
            onChange={e => this.change(e)}
            type="password"
            error={this.state.invalid.password}
            helperText={this.state.errors.password}
            onBlur={e => this.handleBlur(e)}
            fullWidth
          />
          <br />
          <Button
            label="Submit"
            onClick={e => this.onSubmit(e)}
            variant="contained"
            color="inherit"
          >
          Submit
          </Button>
        </form>
    );
  }
}


export default Form;



